<template>
  <button
    ref="buttonElement"
    :class="buttonClasses"
    :disabled="disabled || loading"
    :type="type"
    v-bind="$attrs"
    @click="handleClick"
    @mousedown="handleMouseDown"
    @mouseenter="handleMouseEnter"
    @mouseleave="handleMouseLeave"
  >
    <!-- Loading State -->
    <span v-if="loading" class="mr-2 animate-spin">
      <FontAwesomeIcon icon="spinner" />
    </span>

    <!-- Icon Slot -->
    <span
      v-if="$slots.icon && !loading"
      class="mr-2 transition-transform duration-200"
      ref="iconElement"
    >
      <slot name="icon" />
    </span>

    <!-- Content -->
    <span class="relative z-10">
      <slot />
    </span>

    <!-- Ripple Container -->
    <div ref="rippleContainer" class="pointer-events-none absolute inset-0 overflow-hidden">
      <!-- Ripples will be dynamically added here -->
    </div>

    <!-- Hover Glow Effect -->
    <div
      ref="glowElement"
      :class="[
        'absolute inset-0 opacity-0 transition-opacity duration-300',
        'bg-gradient-to-r from-transparent via-white/20 to-transparent',
        'pointer-events-none',
      ]"
    ></div>

    <!-- Success State Overlay -->
    <Transition
      name="success"
      enter-active-class="transition-all duration-500"
      enter-from-class="opacity-0 scale-0"
      enter-to-class="opacity-100 scale-100"
      leave-active-class="transition-all duration-300"
      leave-from-class="opacity-100 scale-100"
      leave-to-class="opacity-0 scale-0"
    >
      <div
        v-if="showSuccess"
        class="absolute inset-0 flex items-center justify-center rounded-lg bg-green-500 text-white"
      >
        <FontAwesomeIcon icon="check" class="text-lg" />
      </div>
    </Transition>
  </button>
</template>

<script setup lang="ts">
import { computed, ref, nextTick } from 'vue'
import { useAdvancedAnimations } from '@/composables/useAdvancedAnimations'

interface Props {
  variant?: 'primary' | 'secondary' | 'ghost' | 'outline' | 'danger'
  size?: 'sm' | 'md' | 'lg' | 'xl'
  disabled?: boolean
  loading?: boolean
  type?: 'button' | 'submit' | 'reset'
  fullWidth?: boolean
  rounded?: boolean
  ripple?: boolean
  glow?: boolean
  success?: boolean
}

const props = withDefaults(defineProps<Props>(), {
  variant: 'primary',
  size: 'md',
  disabled: false,
  loading: false,
  type: 'button',
  fullWidth: false,
  rounded: false,
  ripple: true,
  glow: true,
  success: false,
})

const emit = defineEmits<{
  click: [event: MouseEvent]
}>()

const { createRippleEffect } = useAdvancedAnimations()

// Template refs
const buttonElement = ref<HTMLElement>()
const rippleContainer = ref<HTMLElement>()
const glowElement = ref<HTMLElement>()
const iconElement = ref<HTMLElement>()

// State
const showSuccess = ref(false)
const isHovered = ref(false)

const buttonClasses = computed(() => {
  const baseClasses = [
    'relative overflow-hidden',
    'inline-flex items-center justify-center font-medium',
    'transition-all duration-300 ease-out',
    'focus:outline-none focus:ring-4 focus:ring-offset-2 focus:ring-offset-white dark:focus:ring-offset-gray-900',
    'disabled:opacity-50 disabled:cursor-not-allowed disabled:pointer-events-none',
    'transform-gpu',
    'active:scale-95',
  ]

  // Enhanced variant styles with gradients
  const variantClasses = {
    primary: [
      'bg-gradient-to-r from-blue-600 to-blue-700 hover:from-blue-700 hover:to-blue-800',
      'text-white shadow-lg shadow-blue-500/25',
      'focus:ring-blue-500',
      'dark:from-blue-500 dark:to-blue-600 dark:hover:from-blue-600 dark:hover:to-blue-700',
      'hover:shadow-xl hover:shadow-blue-500/30',
    ],
    secondary: [
      'bg-gradient-to-r from-gray-100 to-gray-200 hover:from-gray-200 hover:to-gray-300',
      'text-gray-900 shadow-md',
      'focus:ring-gray-500',
      'dark:from-gray-800 dark:to-gray-700 dark:hover:from-gray-700 dark:hover:to-gray-600',
      'dark:text-white',
      'hover:shadow-lg',
    ],
    ghost: [
      'bg-white/80 hover:bg-white/90 text-gray-900',
      'focus:ring-gray-500',
      'dark:bg-white/80 dark:hover:bg-white/90 dark:text-gray-900',
      'backdrop-blur-sm',
    ],
    outline: [
      'border-2 border-gray-300 bg-white/90 hover:bg-white/95 text-gray-900',
      'focus:ring-gray-500',
      'dark:border-gray-600 dark:bg-white/90 dark:hover:bg-white/95 dark:text-gray-900',
      'hover:border-gray-400 dark:hover:border-gray-500',
    ],
    danger: [
      'bg-gradient-to-r from-red-600 to-red-700 hover:from-red-700 hover:to-red-800',
      'text-white shadow-lg shadow-red-500/25',
      'focus:ring-red-500',
      'dark:from-red-500 dark:to-red-600 dark:hover:from-red-600 dark:hover:to-red-700',
      'hover:shadow-xl hover:shadow-red-500/30',
    ],
  }

  // Enhanced size classes
  const sizeClasses = {
    sm: 'px-3 py-1.5 text-sm min-h-[32px]',
    md: 'px-4 py-2 text-sm min-h-[40px]',
    lg: 'px-6 py-3 text-base min-h-[48px]',
    xl: 'px-8 py-4 text-lg min-h-[56px]',
  }

  // Enhanced shape classes
  const shapeClasses = props.rounded ? 'rounded-full' : 'rounded-lg'

  // Width classes
  const widthClasses = props.fullWidth ? 'w-full' : ''

  return [
    ...baseClasses,
    ...variantClasses[props.variant],
    sizeClasses[props.size],
    shapeClasses,
    widthClasses,
  ]
    .filter(Boolean)
    .join(' ')
})

// Enhanced click handler with animations
const handleClick = async (event: MouseEvent) => {
  if (props.disabled || props.loading) return

  // Emit click event
  emit('click', event)

  // Show success state if enabled
  if (props.success) {
    showSuccess.value = true
    setTimeout(() => {
      showSuccess.value = false
    }, 1500)
  }

  // Icon bounce animation
  if (iconElement.value) {
    iconElement.value.style.transform = 'scale(1.2) rotate(10deg)'
    setTimeout(() => {
      if (iconElement.value) {
        iconElement.value.style.transform = ''
      }
    }, 200)
  }
}

// Enhanced mouse down handler with ripple effect
const handleMouseDown = (event: MouseEvent) => {
  if (!props.ripple || props.disabled || props.loading) return

  if (rippleContainer.value) {
    // Dynamic ripple color based on variant
    let rippleColor = 'rgba(255, 255, 255, 0.3)'

    switch (props.variant) {
      case 'primary':
        rippleColor = 'rgba(255, 255, 255, 0.4)'
        break
      case 'secondary':
        rippleColor = 'rgba(59, 130, 246, 0.3)'
        break
      case 'ghost':
        rippleColor = 'rgba(59, 130, 246, 0.2)'
        break
      case 'outline':
        rippleColor = 'rgba(59, 130, 246, 0.2)'
        break
      case 'danger':
        rippleColor = 'rgba(255, 255, 255, 0.4)'
        break
    }

    createRippleEffect(event, rippleContainer.value, rippleColor)
  }
}

// Enhanced mouse enter handler
const handleMouseEnter = () => {
  if (props.disabled || props.loading) return

  isHovered.value = true

  // Glow effect
  if (props.glow && glowElement.value) {
    glowElement.value.style.opacity = '1'
  }

  // Button lift effect
  if (buttonElement.value) {
    buttonElement.value.style.transform = 'translateY(-2px) scale(1.02)'
  }
}

// Enhanced mouse leave handler
const handleMouseLeave = () => {
  isHovered.value = false

  // Remove glow effect
  if (glowElement.value) {
    glowElement.value.style.opacity = '0'
  }

  // Reset button position
  if (buttonElement.value) {
    buttonElement.value.style.transform = ''
  }
}

// Watch for success prop changes
if (props.success) {
  nextTick(() => {
    showSuccess.value = true
    setTimeout(() => {
      showSuccess.value = false
    }, 2000)
  })
}
</script>

<style scoped>
/* Enhanced hover effects */
button:hover {
  background-size: 110% 110%;
}

/* Enhanced focus effects */
button:focus {
  transform: translateY(-1px) scale(1.01);
}

/* Enhanced active effects */
button:active {
  transform: translateY(0) scale(0.98);
}

/* Smooth transitions for disabled state */
button:disabled {
  transform: none !important;
}

/* Loading state styles */
button[disabled] {
  cursor: not-allowed;
}

/* Enhanced success transition */
.success-enter-active {
  transition-delay: 100ms;
}

/* Accessibility improvements */
@media (prefers-reduced-motion: reduce) {
  button {
    transition-duration: 0.01ms !important;
    transform: none !important;
  }

  .success-enter-active,
  .success-leave-active {
    transition-duration: 0.01ms !important;
  }
}

/* High contrast mode support */
@media (prefers-contrast: high) {
  button {
    border: 2px solid;
  }
}

/* Print styles */
@media print {
  button {
    background: white !important;
    color: black !important;
    border: 1px solid black !important;
    box-shadow: none !important;
  }
}
</style>
